              &:before {
                background-size: 100%;
                margin-right: 10px; }

              &.webinars {
                margin-right: 20px;

                &:before {
                  width: 26px;
                  height: 37px; } }


              &.calendar {
                &:before {
                  width: 34px;
                  height: 32px; } } } }
          // > ul

          .assign-now {
            font-size: 12.5px;

            &:after {
              display: none; } }
 }          // .assign-now
        // .info

