              font-size: 12px; } }

          .embed-frm {
            margin-top: 3.7vh;

            .hs-form {
              .hs-form-required {
                font-size: 13px; }

              .actions {
                .hs-button {
                  -webkit-box-shadow: none;
                  -moz-box-shadow: none;
                  box-shadow: none;

                  width: 30.47vw;
                  min-width: 234px;
                  height: 53px;
                  background-color: map-get($colors, cerulean);
                  color: #fff;

                  &:hover {
                    background-color: map-get($colors, cerulean);
                    color: #fff; } } }

 }              // .hs-input
 }            // .hs-form
 }          // .embed-form
 }        // .form
 }      // .content
 }    // .container
 }  // #top
// media (max-width: 767px)
